import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import { useFormik } from 'formik';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Divider from '@mui/material/Divider';

import { DateField, Loader, SelectField, TextField } from '../components';

import { OwnerDriverQuery } from '../api';
import { useSnackbar } from '../hooks';
import {
  CreateOwnerDriver,
  LangPriority,
  LangPriorities,
} from '../utils/dataTypes';
import FileList from '../components/filesList';

const initialValues = {
  fullName: undefined,
  birthDate: undefined,
  citizenship: undefined,
  languagePriority: 'EN' as LangPriority,
  driverLicenceNumber: undefined,
  driverLicenceState: undefined,
  driverLicenceExp: undefined,
  idDocId: undefined,
  idDocType: undefined,
  idDocExp: undefined,
  hiredBy: undefined,
  hireDate: undefined,
  snn: undefined,
  company: undefined,
  insurancePolicy: undefined,
  insurancePolicyExp: undefined,
  address: undefined,
  phone: undefined,
  phone2: undefined,
  email: undefined,
  emergencyContactName: undefined,
  emergencyContactRel: undefined,
  emergencyContactPhone: undefined,
  appLogin: undefined,
  appPass: undefined,
  notes: undefined,
};

export default function ViewOwnerDriverForm() {
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading: loading,
    isFetching: fetching,
    data,
  } = useQuery({
    queryKey: ['ownerDriver', pathname.split('/')[3]],
    queryFn: OwnerDriverQuery,
    keepPreviousData: false,
    retry: false,
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<CreateOwnerDriver>({
    initialValues: {
      ...initialValues,
      ...data,
    },
    enableReinitialize: true,
    onSubmit: () => {
      return;
    },
  });

  const handleClose = () => {
    navigate(-1);
  };

  const handleEdit = () => {
    navigate(pathname.replace('view', 'edit'), { replace: true });
  };

  return (
    <Box sx={{ height: '100%' }}>
      <form
        onSubmit={formik.handleSubmit}
        onReset={formik.handleReset}
        style={{ height: '100%' }}
      >
        <Box sx={{ p: 3, height: '100%' }}>
          <Divider />
          <Box
            key="controls"
            sx={{
              maxHeight: 'calc(100% - 70px)',
              overflowX: 'hidden',
              overflowY: 'scroll',
            }}
          >
            <Box key="topControls" sx={{ p: 1, paddingBottom: 0 }}>
              <TextField
                fieldName="fullName"
                label="First name/Last name"
                disabled
                formik={formik}
              />
            </Box>
            <Box
              key="centerControls"
              sx={{ display: 'inline-flex', width: '100%' }}
            >
              <Box
                key="left"
                sx={{ p: 1, paddingTop: 0, paddingBottom: 0, width: '50%' }}
              >
                <DateField
                  fieldName="birthDate"
                  label="Birth Date"
                  disabled
                  formik={formik}
                />
                <SelectField
                  fieldName="languagePriority"
                  label="Language Priority"
                  items={LangPriorities}
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="driverLicenceState"
                  label="Driver Licence State"
                  disabled
                  formik={formik}
                />
                <DateField
                  fieldName="driverLicenceExp"
                  label="Driver Licence Exp"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="idDocType"
                  label="Id Doc Type"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="hiredBy"
                  label="Hired By"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="company"
                  label="Company"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="address"
                  label="Home address"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="email"
                  label="Email"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="emergencyContactName"
                  label="Emergency Contact Name"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="emergencyContactPhone"
                  label="Emergency Contact Phone"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="appLogin"
                  label="Mobile App Login"
                  disabled
                  formik={formik}
                />
              </Box>
              <Box
                key="right"
                sx={{ p: 1, paddingTop: 0, paddingBottom: 0, width: '50%' }}
              >
                <TextField
                  fieldName="citizenship"
                  label="Citizenship"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="driverLicenceNumber"
                  label="Driver Licence Number"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="idDocId"
                  label="Id Doc Id"
                  disabled
                  formik={formik}
                />
                <DateField
                  fieldName="idDocExp"
                  label="Id Doc Exp"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="snn"
                  label="SSN#"
                  disabled
                  formik={formik}
                />
                <DateField
                  fieldName="hireDate"
                  label="Hire Date"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="insurancePolicy"
                  label="Insurance Policy"
                  disabled
                  formik={formik}
                />
                <DateField
                  fieldName="insurancePolicyExp"
                  label="Insurance Policy Exp"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="phone"
                  label="Phone"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="phone2"
                  label="Phone2"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="emergencyContactRel"
                  label="Emergency Contact Rel"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="appPass"
                  label="Mobile App Pass"
                  disabled
                  formik={formik}
                />
              </Box>
            </Box>
            <Box
              key="bottomControls"
              sx={{ p: 1, paddingBottom: 0, paddingTop: 0 }}
            >
              <TextField
                fieldName="notes"
                label="Notes"
                multiline
                disabled
                formik={formik}
              />
              <FileList objectId={data?.id} objectType="Person" disabled />
            </Box>
          </Box>
          <Divider />
          <Box key="buttons" sx={{ paddingTop: 3, display: 'flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleEdit}
              >
                Edit
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={loading || fetching} />
    </Box>
  );
}
